.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  padding: 12px;
  box-sizing: border-box;
}

.textarea {
  max-width: 500px;
  width: 94%;
  margin-bottom: 16px;
}

.button-container {
  display: flex;
  flex-direction: column;
  gap: 8px;
  margin: 8px 0;
}

.button {
  width: 94%;
  max-width: 256px;
}

.preview {
  margin-top: 16px;
  width: 100%;
  max-width: 500px;
  border: 1px solid #ccc;
  padding: 8px;
  border-radius: 4px;
  background-color: #f9f9f9;
}

@media (min-width: 800px) {
  .App {
    flex-direction: row; /* Two-column layout on PC */
    align-items: flex-start;
  }

  .textarea,
  .preview {
    flex: 1;
    margin-right: 16px;
  }

  .button-container {
    align-self: flex-start;
  }
  .preview {
    margin-top: 0px;
  }
}
